









  import { Component, Prop, Vue } from 'vue-property-decorator'

  @Component
  export default class Container extends Vue {
    @Prop({default: '窗口'}) title !:string
    @Prop({default: false}) isTop !:boolean
    @Prop({default: 800}) width !:number
    @Prop({default: 600}) height !:number

    isScreen :boolean = false
    isAnimated :boolean = true

    private drag(e :any) :void {
      this.isAnimated = false
      let odiv = e.target.parentNode
      let cls = odiv.getAttribute('data-id')
      if(cls !== "container") {
        odiv = e.target.parentNode.parentNode
      }
      let x = e.clientX - odiv.offsetLeft
			let y = e.clientY - odiv.offsetTop
      document.onmousemove = (e) => {

        let left = e.clientX - x
        let top = e.clientY - y
        if(top <= 20) {
          top = 20
        } else {
          top = e.clientY - y
        }
        odiv.style.left = left + 'px'
        odiv.style.top = top + 'px'
      }
      document.onmouseup = () => {
        this.isAnimated = true
				document.onmousemove = document.onmouseup = null
			}
    }
    //关闭窗口
    private close() :void {
      this.isScreen = false
      this.$emit("closeContainer")
    }
    //全屏
    private screen() :void {
      this.isScreen = !this.isScreen
    }
    //当前激活的窗口设置在最顶层
    private onFocus(e :any) :void {
      this.isTop = true
    }
    mounted() :void {

    }
  }
